

















































































































































import Vue from "vue";
import KLoading from "../../components/KLoading.vue";
import KDialog from "../../components/KDialog.vue";
import KDataList from "../../components/KDataList.vue";

import Component from "vue-class-component";
import { Watch } from "vue-property-decorator";

import { Display } from "../../configuration/display";
import { Storage } from "../../configuration/storage";

import { Header, HeaderIcon, HeaderAction } from "../../models/header";
import { Headers } from "../../models/headers";

import {
  ISolicitacaoAprovacaoItensListaView,
  ISolicitacaoAprovacaoItensListaController
} from "../../contracts/SolicitacaoAprovacao/SolicitacaoAprovacaoItensListaContract";
import { SolicitacaoAprovacaoItensListaController } from "../../controllers/SolicitacaoAprovacao/SolicitacaoAprovacaoItensListaController";

@Component({
  components: {
    KLoading,
    KDialog,
    KDataList
  }
})
export default class SolicitacaoForm extends Vue
  implements ISolicitacaoAprovacaoItensListaView {
  _controller?: ISolicitacaoAprovacaoItensListaController;

  dataVencimentoMenu = false;

  isMobile = false;

  isSucesso = false;

  dialogReprovar = false;

  visualizarProdutoDialog = false;
  justificativaProduto = "";

  msgReprovar = "";

  dialogLoading = false;
  dialogMessage = false;
  textError = "";

  itensFiliais = [];
  itensCentroCustos = [];
  itensProdutos = [
    {
      nome: "",
      codigo: "",
      quantidade: ""
    }
  ];
  itensFretes = [];
  itensColaboradores = [];

  produtoSelecionado = {
    quantidade: ""
  };

  headers = new Headers([
    new Header("Número", "numero", false, false),
    new Header("Produto/Serviço", "produto", false, false),
    new Header("Descrição", "descricao", false, false),
    new Header("Quantidade", "quantidade", false, false),
    new Header("Ação", "acao", true, false)
  ]);

  idSolicitacao = "";

  itens = [
    {
      numero: "0",
      produto: "",
      descricao: "",
      quantidade: ""
    }
  ];

  solicitacao = {
    solicitante: "",
    filial: "",
    centroCusto: "",
    frete: "",
    dataVencimento: "",
    descricao: "",
    produtos: this.itens,
    user: 0
  };

  produto = {
    produto: "",
    quantidade: ""
  };

  loading(visible: boolean): void {
    this.dialogLoading = visible;
  }

  fecharDialogMessage() {
    this.dialogMessage = false;
    if (this.isSucesso) {
      this.$router.push("/solicitacaoAprovar");
    }
  }

  showMsg(msg: string, modo: number): void {
    this.textError = msg;
    this.dialogMessage = true;
    if (modo == 1) {
      this.isSucesso = true;
    }
  }

  enviarAprovacao(aprovado: boolean) {
    if (aprovado) {
      this._controller!!.enviarAprovacao(
        this.idSolicitacao,
        true,
        parseInt(Storage.get("User")!!),
        ""
      );
    } else {
      this.msgReprovar = "";
      this.dialogReprovar = true;
    }
  }

  confirmarReprovacao() {
    this.dialogReprovar = false;
    this._controller!!.enviarAprovacao(
      this.idSolicitacao,
      false,
      parseInt(Storage.get("User")!!),
      this.msgReprovar
    );
  }

  showProdutos(produtos: any) {
    this.itens = produtos;
  }

  visualizarProduto(item: any) {
    this.justificativaProduto = "Justificativa: " + item.justificativa;
    this.visualizarProdutoDialog = true;
  }

  fecharVisualizarProdutoDialog() {
    this.visualizarProdutoDialog = false;
  }

  carregarDtos(
    itensFiliais: [],
    itensCentroCustos: [],
    itensProdutos: [],
    itensFretes: [],
    itensColaboradores: []
  ): void {
    this.itensFiliais = itensFiliais;
    this.itensCentroCustos = itensCentroCustos;
    this.itensProdutos = itensProdutos;
    this.itensFretes = itensFretes;
    this.itensColaboradores = itensColaboradores;

    if (this.$route.params.id) {
      this.idSolicitacao = this.$route.params.id;
      this._controller!.getProdutoForEditar(this.idSolicitacao);
    }
  }

  showProdutoForEditar(item: any, itens: any) {
    this.solicitacao = item;
    this.itens = itens;
  }

  created() {
    this._controller = new SolicitacaoAprovacaoItensListaController(this);

    this._controller.carregarDtos();

    this.isMobile = Display.isMobile();
    this.itens = [];
  }
}
