import {
    ISolicitacaoAprovacaoItensListaView,
    ISolicitacaoAprovacaoItensListaController
} from "../../contracts/SolicitacaoAprovacao/SolicitacaoAprovacaoItensListaContract";
import axios from "../../plugins/axios"

import { Header, HeaderIcon, HeaderAction } from "../../models/header";
import { Headers } from "../../models/headers";

export class SolicitacaoAprovacaoItensListaController implements ISolicitacaoAprovacaoItensListaController {

    _view: ISolicitacaoAprovacaoItensListaView


    _produtos = [
        {
            codigo: "",
            nome: "",
            um: ""
        }
    ]

    _lista = [{
        numero: "",
        produto: "",
        descricao: "",
        quantidade: "",
        acao: [new HeaderAction("", "", "", "", "")]
    }]

    constructor(view: ISolicitacaoAprovacaoItensListaView) {
        this._view = view
    }


    getProduto(codigo: string): any {
        if (codigo) {
            return this._produtos.filter(
                e => e.codigo === codigo
            )[0];
        } else {
            return { quantidade: "" }
        }
    }


    carregarDtos(): void {
        this._view.loading(true)

        axios.get('Solicitacao/GetParametros').then((response: any) => {
            this._view.loading(false)
            if (response.data.sucesso) {
                let data = response.data.tRetorno;
                this._produtos = data.produtos
                this._view.carregarDtos(data.filiais, data.centroCustos, data.produtos, data.fretes, data.colaboradors)
                this._lista = []
            } else {
                this._view.showMsg(response.data.message, 0)
            }
        }).catch((e: any) => {
            this._view.loading(false)
            this._view.showMsg(e, 0)
        })
    }

    enviarAprovacao(id: string, aprovado: boolean, codAprovador: number, msg: string): void {
        this._view.loading(true)

        axios.post('Solicitacao/Aprovacao/' + id, {
            codAprovador: codAprovador,
            aprovado: aprovado,
            mensagem: msg
        }).then((response: any) => {
            this._view.loading(false)
            if (response.data.sucesso) {
                this._view.showMsg(response.data.message, 1)
            } else {
                this._view.showMsg(response.data.message, 0)
            }
        }).catch((e: any) => {
            this._view.loading(false)
            this._view.showMsg(e, 0)
        })
    }


    getProdutoForEditar(id: string): void {
        this._view.loading(true)

        axios.get('Solicitacao/ById/' + id).then((response: any) => {
            this._view.loading(false)
            if (response.data) {
                response.data.dataVencimento = response.data.dataVencimento.split('T')[0]

                this._lista = [];
                let i = 1;
                response.data.itens.forEach((element: any) => {
                    let produto = {
                        numero: i.toLocaleString("pt-BR", { minimumIntegerDigits: 3 }),
                        produto: element.codProduto,
                        descricao: this._produtos.filter(e => e.codigo == element.codProduto)[0].nome,
                        quantidade: element.quantidade,
                        um: this._produtos.filter(e => e.codigo == element.codProduto)[0].um,
                        justificativa: element.justificativa,
                        acao: [new HeaderAction(
                            "mdi-eye",
                            "primary",
                            "Visualizar",
                            "visualizarProduto",
                            "200px"
                        )]
                    }

                    this._lista.push(produto);
                    i++;
                });
                this._view.showProdutoForEditar(response.data, this._lista)
            } else {
                this._view.showMsg("Não encontrado. ", 0)
            }
        }).catch((e: any) => {
            this._view.loading(false)
            this._view.showMsg(e, 0)
        })
    }


}